import { Component, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AppService, StoreService } from '@monorepo/utils';
import { HfCardComponent, HfInputComponent, HfPriceComponent } from '@monorepo/ui';
import { Router } from '@angular/router';

@Component({
  selector: 'app-order-details-bar',
  standalone: true,
  imports: [CommonModule, HfCardComponent, HfInputComponent, HfPriceComponent],
  templateUrl: './order-details-bar.component.html',
  styleUrl: './order-details-bar.component.scss',
})
export class OrderDetailsBarComponent implements OnInit {

  orderParams: any;

  constructor(
    private storeService: StoreService,
    private router: Router,
    public appService: AppService,
  ) { }

  ngOnInit() {
    if (this.appService.isBrowserRendering) {
      this.storeService.getValueFromStore('orderParams', (orderParams) => {
        this.orderParams = orderParams;
      });
    }
  }

  goToOilProviderPage() {
    const url = `/oil-select-provider/${this.orderParams['zipCode']}/0/0/4/${this.orderParams['quantity']}`;
    this.router.navigate([url]);
  }
}
